<template>
  <div class="d_bg">
    <!--    导航栏-->
    <NavBar class="d_nav">
      <i slot="left" class="el-icon-back" @click="back"></i>
      <div slot="center">文章详情</div>
      <div slot="right" class="d_right">
        <img src="@/assets/img/details/like.png">
        <img src="@/assets/img/details/share.png">
      </div>
    </NavBar>
    <!--    正文-->
    <BetterScroll class="content" ref="scroll" :probe-type="3" @scroll="contentScroll" :pull-up-load="true"
                  @pullingUp="loadMore">
      <div class="d_body">
        <span class="title">{{ goods.title }}</span>
        <div class="subtitle">
          <span>初声</span>
          <span>{{ goods.updatedAt }}</span>
        </div>
        <el-card class="d_card">
          <img src="@/assets/img/details/baby.png">
        </el-card>
        <div class="body">
          <span v-html="goods.content"></span>
        </div>
      </div>
    </BetterScroll>
    <BackTop @click.native="backTop" v-show="isShow"/>
  </div>
</template>

<script>
import NavBar from "@/components/navbar/NavBar";
import BetterScroll from "@/components/betterScroll/BetterScroll";
import BackTop from "@/components/backTop/BackTop";

export default {
  name: "Details",
  data() {
    return {
      id: null,
      goods: [],
      isShow: false,
    }
  },
  components: {
    NavBar,
    BetterScroll,
    BackTop
  },
  methods: {
    //获取文章
    getArticle() {
      this.$http.get('article?id=' + this.id).then(res => {
            this.goods = res.data.data.records
      }).catch(err => err)
    },
    back() {
      this.$router.go(-1)
    },
    backTop() {
      this.$refs.scroll.scrollTo(0, 0)
    },
    contentScroll(position) {
      this.isShow = (-position.y) > 240
    },
    loadMore() {
      this.$refs.scroll.refresh()
    }
  },
  created() {
    this.id = this.$route.params.id
    this.$nextTick(()=>{
      this.getArticle();
    })
  },
}
</script>

